import {
    createApp
} from 'vue'
import App from './App.vue'
import '@/assets/styles/style.scss';

import {
    languages
} from '@/i18n/index'
import {
    createI18n
} from 'vue-i18n'

let localStorageLang = localStorage.lang

const i18n = createI18n({
    legacy: true,
    locale: localStorageLang || 'ru',
    messages: languages
})
createApp(App).use(i18n).mount('#app')