<template>
    <Transition name="modal">
        <div class="modal" @click="closeModel">
            <div class="modal__block" @click.stop>
                <h3 class="modal__block_title">{{ edit ? $t('changeNote') : $t('addNote') }} </h3>
                <label>
                    <span>Title</span>
                    <input type="text" placeholder="title" v-model="title">
                </label>
                <label>
                    <span>Content</span>
                    <textarea placeholder="content" v-model="text"></textarea>
                </label>
                <div class="modal__block_btns">
                    <button class="modal__block_btns-btn del" @click="closeModel"></button>
                    <button v-if="!edit" class="modal__block_btns-btn edit" @click="addNote">{{ $t('add') }}</button>
                    <button v-else class="modal__block_btns-btn edit" @click="changeNote">{{ $t('change') }}</button>
                </div>
            </div>
        </div>
    </Transition>
</template> 

<script>
import { v4 as uuidv4 } from 'uuid';
export default {
    data() {
        return {
            title: '',
            text: '',
        }
    },
    methods: {
        closeModel() {
            this.$emit('close')
            this.title = this.text = ''
        },
        addNote() {
            if (this.title != '' && this.text != '') {
                const note = {
                    id: uuidv4(),
                    title: this.title,
                    text: this.text,
                    date: new Date().toLocaleDateString(),
                }
                this.$emit('addNote', note)
                this.closeModel()

            }

        },
        changeNote() {
            const newNote = {
                id: this.editedNote.id,
                title: this.title,
                text: this.text,
                date: new Date().toLocaleDateString(),
            }
            this.$emit('changedNote', newNote)
            this.closeModel()
        },
    },
    props: {
        edit: Boolean,
        editedNote: Object
    }
}
</script>

